 .datepicker {
  background-color: $white;
  border: 1px solid rgba(28, 39, 60, 0.2);
  font-family: "Roboto", sans-serif;
  font-size: inherit;
  margin: 1px 0 0;
  display: none;
  width: auto !important;
  z-index: 5 !important;
  direction: ltr;

  &.dropdown-menu {
    color: $dark;
  }
}

.datepicker-inline {
  width: 220px;
}

.datepicker-rtl {
  direction: rtl;

  &.dropdown-menu {
    left: auto;
  }

  table tr td span {
    float: right;
  }
}

.datepicker-dropdown {
  top: 0;
  left: 0;

  &:before {
    content: '';
    display: inline-block;
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-bottom: 7px solid #f0f3fa;
    border-top: 0;
    border-bottom-color: $black-2;
    position: absolute;
  }

  &:after {
    content: '';
    display: inline-block;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
    border-bottom: 6px solid #f0f3fa;
    border-top: 0;
    position: absolute;
  }

  &.datepicker-orient-left {
    &:before {
      left: 6px;
    }

    &:after {
      left: 7px;
    }
  }

  &.datepicker-orient-right {
    &:before {
      right: 6px;
    }

    &:after {
      right: 7px;
    }
  }

  &.datepicker-orient-bottom {
    &:before {
      top: -7px;
    }

    &:after {
      top: -6px;
    }
  }

  &.datepicker-orient-top {
    &:before {
      bottom: -7px;
      border-bottom: 0;
      border-top: 7px solid #f0f3fa;
    }

    &:after {
      bottom: -6px;
      border-bottom: 0;
      border-top: 6px solid #f0f3fa;
    }
  }
}

.datepicker {
  table {
    padding: 6px 10px;
    margin: 0;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border-collapse: inherit;
  }

  td {
    text-align: center;
    padding: 6px 9px;
    -webkit-border-radius: 0px;
    -moz-border-radius: 0px;
    border-radius: 0px;
    border: none;
    font-size: 12px;
    font-weight: 400;
    color: #070510;
  }

  .datepicker-switch {
    text-align: center;
    color: $dark !important;
    font-weight: 700;
    font-size: 13px;
    text-transform: uppercase;
    letter-spacing: 1px;
    opacity: inherit !important;

    &:hover {
      color: $dark !important;
      background: transparent !important;
    }
  }

  .prev:hover, .next:hover, tfoot tr th:hover {
    color: $dark !important;
    background: transparent !important;
  }

  th {
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 700;
    letter-spacing: 1px;
    padding: 6px 9px;
    color: $dark;
  }
}

.table-striped .datepicker table tr {
  td, th {
    background-color: transparent;
