 .jctkr-wrapper {
  box-sizing: border-box;

  * {
    box-sizing: border-box;
  }

  display: inline-block;
  position: relative;
  width: 100%;
  height: 90px;
  vertical-align: top;
  overflow: hidden;

  ul {
    position: absolute;
    left: 0;
    font-size: 0;
    -webkit-transition: opacity 1s;
    transition: opacity 1s;
    overflow: visible;
  }

  &.jctkr-initialized ul {
    opacity: 100;
    height: 90px;
    overflow: hidden;
  }

  ul li .crypto-card {
    display: inline-block;
    font-family: sans-serif;
    font-size: 16px;
    margin: 0 0.3rem;
    padding: 13px 30px;
    background: $white;
    line-height: 1.4;
    box-shadow: 5px 7px 12px -5px #9f9fbb;
    border-radius: 3px;
    display: inline;
    float: left;
    white-space: nowrap;
  }
}

.jctkr-label {
  display: inline-block;
}

.news-crypto li {
  list-style: none;
}