/* SmartWizard Theme: Circles */

.sw-theme-circles {
    .sw-container {
      min-height: 300px;
    }
  
    .step-content {
      padding: 10px 0;
      background-color: #FFF;
      text-align: left;
    }
  
    .sw-toolbar {
      background: $white;
      padding-left: 10px;
      padding-right: 10px;
      margin-bottom: 0 !important;
    }
  }
  
  .nav.nav-tabs.step-anchor {
    border-bottom: 0 !important;
  }
  
  .sw-theme-circles {
    .sw-toolbar-top {}
  
    .sw-toolbar-bottom {
      border-top-color: #ddd !important;
      border-bottom-color: #ddd !important;
    }
  
    > ul.step-anchor {
      position: relative;
      background: $white;
      border: none;
      list-style: none;
      margin-bottom: 40px;
  
      &:before {
        content: " ";
        position: absolute;
        top: 50%;
        bottom: 0;
        width: 100%;
        height: 5px;
        background-color: #f5f5f5;
        border-radius: 3px;
        z-index: 0;
      }
  
      > li {
        border: none;
        margin-left: 40px;
        z-index: 98;
  
        > a {
          border: 2px solid #f5f5f5;
          background: #f5f5f5;
          width: 75px;
          height: 75px;
          text-align: center;
          padding: 28px 0;
          border-radius: 50%;
          -webkit-box-shadow: inset 0px 0px 0px 3px $white !important;
          box-shadow: inset 0px 0px 0px 3px $white !important;
          text-decoration: none;
          outline-style: none;
          z-index: 99;
          color: #5c6287;
          background: #f5f5f5;
          line-height: 1;
  
          &:hover {
            color: #5c6287;
            background: #f5f5f5;
            border-width: 2px;
          }
  
          > small {
            position: relative;
            bottom: -40px;
            color: #ccc;
          }
        }
  
        &.clickable > a:hover {
          color: #4285F4 !important;
        }
  
        &.active > a {
          border-color: #7673e6;
          color: $white;
          background: #7673e6;
  
          > small {
            color: #7673e6;
          }
        }
  
        &.done > a {
          border-color: #f4f5f5;
          color: #5c6287;
          background: #f4f5f5;
  
          > small {
            color: #f4f5f5;
          }
        }
  
        &.danger > a {
          border-color: #d9534f;
          color: #d9534f;
          background: $white;
  
          > small {
            color: #d9534f;
          }
        }
  
        &.disabled > a {
          color: #eee !important;
  
          &:hover {
            color: #eee !important;
          }
        }
      }
    }
  }
  
  /* Responsive CSS */
  @media screen and (max-width: 480px) {
    .sw-theme-circles > ul.step-anchor > li {
      > a {
        width: 100%;
        height: 100%;
        border-radius: 0;
      }
  
      margin: 0 auto;
      margin-bottom: 1rem;
  
      &:last-child {
        margin-bottom: 0;
      }
    }
  }