			float: left;
			position: relative;
			top: 0;
			margin: 1px 0.2em 0 0;
			border-bottom-width: 0;
			padding: 0;
			white-space: nowrap;
		}

		.ui-tabs-anchor {
			float: left;
			padding: 0.5em 1em;
			text-decoration: none;
		}

		li {
			&.ui-tabs-active {
				margin-bottom: -1px;
				padding-bottom: 1px;

				.ui-tabs-anchor {
					cursor: text;
				}
			}

			&.ui-state-disabled .ui-tabs-anchor,
			&.ui-tabs-loading .ui-tabs-anchor {
				cursor: text;
			}
		}
	}
}

.ui-tabs-collapsible .ui-tabs-nav li.ui-tabs-active .ui-tabs-anchor {
	cursor: pointer;
}

.ui-tabs .ui-tabs-panel {
	display: block;
	border-width: 0;
	padding: 1em 1.4em;
	background: none;
}

.ui-tooltip {
	padding: 8px;
	position: absolute;
	z-index: 9999;
	max-width: 300px;
}

body .ui-tooltip {
	border-width: 2px;
}

/* Component containers
  ----------------------------------*/

.ui-widget {
	// font-family: Arial,Helvetica,sans-serif;
	font-size: 1em;

	.ui-widget {
		font-size: 1em;
	}

	input,
	select,
	textarea,
	button {
		//   font-family: Arial,Helvetica,sans-serif;
		font-size: 1em;
	}

	&.ui-widget-content {
		border: 1px solid #c5c5c5;
		overflow: auto;
	}
}

.ui-widget-content {
	border: 1px solid #dddddd;
	background: $white;
	color: #333333;

	a {
		color: #333333;
	}
}

.ui-widget-header {
	border: 1px solid #dddddd;
	background: #e9e9e9;
	color: #333333;
	font-weight: bold;

	a {
		color: #333333;
	}
}

/* Interaction states
  ----------------------------------*/

.ui-state-default,
.ui-widget-content .ui-state-default,
.ui-widget-header .ui-state-default,
.ui-button {
	border: 1px solid #c5c5c5;
	font-weight: normal;
	color: #454545;
}

html .ui-button.ui-state-disabled {
	&:hover,
	&:active {
		border: 1px solid #c5c5c5;
		background: #f6f6f6;
		font-weight: normal;
		color: #454545;
	}
}

.ui-state-default a {
	color: #454545;
	text-decoration: none;

	&:link,
	&:visited {
		color: #454545;
		text-decoration: none;
	}
}

a {
	&.ui-button,
	&:link.ui-button,
	&:visited.ui-button {
		color: #454545;
		text-decoration: none;
	}
}

.ui-button {
	color: #454545;
	text-decoration: none;
}

.ui-state-hover,
.ui-widget-content .ui-state-hover,
.ui-widget-header .ui-state-hover,
.ui-state-focus,
.ui-widget-content .ui-state-focus,
.ui-widget-header .ui-state-focus {
	border: 1px solid #cccccc;
	background: #ededed;
	font-weight: normal;
	color: #2b2b2b;
}

.ui-button {
	&:hover,
	&:focus {
		border: 1px solid #cccccc;
		background: #ededed;
		font-weight: normal;
		color: #2b2b2b;
	}
}

.ui-state-hover a {
	color: #2b2b2b;
	text-decoration: none;

	&:hover,
	&:link,
	&:visited {
		color: #2b2b2b;
		text-decoration: none;
	}
}

.ui-state-focus a {
	color: #2b2b2b;
	text-decoration: none;

	&:hover,
	&:link,
	&:visited {
		color: #2b2b2b;
		text-decoration: none;
	}
}

a.ui-button {
	&:hover,
	&:focus {
		color: #2b2b2b;
		text-decoration: none;
	}
}

.ui-visual-focus {
	box-shadow: 0 0 3px 1px rgb(94, 158, 214);
}

.ui-state-active,
.ui-widget-content .ui-state-active,
.ui-widget-header .ui-state-active,
a.ui-button:active {
	border: 1px solid $primary;
	background: $primary;
	font-weight: normal;
	color: $white !important;
}

.ui-button {
	&:active,
	&.ui-state-active:hover {
		border: 1px solid $primary;
		background: $primary;
		font-weight: normal;
		color: $white;
	}
}

.ui-icon-background {
	border: $primary;
	background-color: $white;
}

.ui-state-active {
	.ui-icon-background {
		border: $primary;
		background-color: $white;
	}

	a {
		color: $white;
		text-decoration: none;

		&:link,
		&:visited {
			color: $white;
			text-decoration: none;
		}
	}
}

/* Interaction Cues
  ----------------------------------*/

.ui-state-highlight,
.ui-widget-content .ui-state-highlight,
.ui-widget-header .ui-state-highlight {
	border: 1px solid #dad55e;
	background: #fffa90;
	color: #777620;
}

.ui-state-checked {
	border: 1px solid #dad55e;
	background: #fffa90;
}

.ui-state-highlight a,
.ui-widget-content .ui-state-highlight a,
.ui-widget-header .ui-state-highlight a {
	color: #777620;
}

.ui-state-error,
.ui-widget-content .ui-state-error,
.ui-widget-header .ui-state-error {
	border: 1px solid #f1a899;
	background: #fddfdf;
	color: #5f3f3f;
}

.ui-state-error a,
.ui-widget-content .ui-state-error a,
.ui-widget-header .ui-state-error a,
